<script>
/**
 * portal
 * 
 * props:
 *  headerConfig: src/components/header/Header.vue
 *  sideMenuConfig: src/components/menu/Menu.vue
 *  tabConfig:
 */
import SideMenu from '@/components/menu'
import TopHeader from '@/components/header'
import XsTab from '@/components/tab'
export default {
  name: 'Portal',
  props: {
    headerConfig: {
      type: Object,
      default: () => {}
    },
    sideMenuConfig: {
      type: Object,
      default: () => {}
    },
    tabConfig: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {
      isCollapse: false
    }
  },
  components: {
    SideMenu,
    TopHeader,
    XsTab
  },
  methods: {
    renderHeader () {
      // 
      const { props = {}, on } = this.headerConfig || {}
      const h = this.$createElement
      return h('top-header', {
        attrs: { ...props },
        on
      })
    },
    renderContainer () {
      // 
      const h = this.$createElement
      return h('el-container', {
        class: 'main-container'
      }, [
        this.renderSideMenu(),
        this.renderMain()
      ])
    },
    renderSideMenu () {
      // 
      const h = this.$createElement
      return h('el-aside', {
        props: {
          width: 'auto'
        }
      }, [
        h('el-scrollbar', {
          style: {
            height: 'calc(100% - 40px)'
          }
        }, [
          this.renderMenu()
        ]),
        this.renderCollapse()
      ])
    },
    renderCollapse () {
      // 
      const h = this.$createElement
      return h('div', {
        class: 'collapse',
        on: {
          click: () => { this.isCollapse = !this.isCollapse }
        }
      }, [
        h('i', {
          class: this.isCollapse ? 'el-icon-s-unfold' : 'el-icon-s-fold'
        })
      ])
    },
    renderMenu () {
      // 
      const { props = {}, on } = this.sideMenuConfig || {}
      const h = this.$createElement
      return h('side-menu', {
        class: 'el-menu-vertical',
        attrs: {
          ...props,
          collapse: this.isCollapse
        },
        on
      })
    },
    renderMain () {
      // 
      const { props = {}, on } = this.tabConfig || {}
      const h = this.$createElement
      return h('el-main', [
        h('xs-tab', {
          attrs: {
            ...props
          },
          on
        }),
        h('div', {
          class: 'content'
        }, [
          h('router-view')
        ])
      ])
    }
  },
  render (h) {
    return h('el-container', {
      class: 'container',
      props: {
        direction: 'vertical'
      }
    }, [
      this.renderHeader(),
      this.renderContainer()
    ])
  }
}
</script>
<style lang="scss" scoped>
$header-height: 60px; // element-ui60px
.container {
  height: 100%;
}
.main-container {
  height: calc(100% - #{$header-height});
}
.content {
  height: calc(100% - 40px);
  overflow: auto;
  box-sizing: border-box;
  padding: 12px 0;
  background: #f0f0f0;
}
.el-aside {
  height: 100%;
  background: #212529;
  color: #ADB5BD;
  box-sizing: border-box;
  ::v-deep .el-menu-vertical:not(.el-menu--collapse) {
    width: 210px;
    box-sizing: border-box;
  }
  ::v-deep .el-menu-item.is-active {
    background-color: #155DDF !important;
  }
  .collapse {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    cursor: pointer;
    background: #343A40;
    font-size: 20px;
  }
}
.el-main {
  padding: 0 20px;
  overflow: hidden;
  background: #fff;
}
</style>
